import PageTitle from "../Components/PageTitle"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageContent from "../Components/PageContent"
import Card from "../../Components/Card"
import FormGroup from "../../Components/FormGroup"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"
import { post } from "../../Functions/Api"
import useUserData from "../../Hooks/useUserData"
import { PopupAlert } from "../../Functions/Alerts"
import { useNavigate } from "react-router-dom"

const TambahAnggota = () => {
    const { no_kk, role } = useUserData()
    const navigate = useNavigate()

    const formTambahAnggota: FormGroupT[] = [
        {
            label: "Nama",
            type: "text",
            name: "nama",
            id: "nama",
            placeholder: "Budi Santoso"
        },
        {
            label: "Nomor Whatsapp",
            type: "number",
            name: "no_whatsapp",
            id: "no_whatsapp",
            placeholder: "08237623363"
        },
        {
            label: "NIK",
            type: "number",
            name: "nik",
            id: "nik",
            placeholder: "1771080503010001"
        }
    ]

    const tambahData: React.FormEventHandler<HTMLFormElement> = async (evt) => {
        evt.preventDefault()

        const res = await post(
            `anggota-keluarga?no_kk=${no_kk}`,
            evt.currentTarget
        )
        if (res.ok) {
            PopupAlert.fire({
                icon: "success",
                text: "Berhasil Menambahkan Anggota Keluarga"
            })

            navigate(`/${role}/data-keluarga`)
        }
    }

    return (
        <>
            <PageTitle title="Data Keluarga" />
            <ButtonBack />
            <PageContent>
                <Card
                    cardHeader={
                        <h4 className="card-title text-success">
                            Tambah Data Anggota Keluarga
                        </h4>
                    }
                    cardBody={
                        <form onSubmit={tambahData}>
                            <div className="row">
                                <div className="col-md-6">
                                    {formTambahAnggota.map((e, i) => (
                                        <FormGroup {...e} key={i} />
                                    ))}
                                    <ButtonSubmit>
                                        Tambah Anggota Keluarga
                                    </ButtonSubmit>
                                </div>
                            </div>
                        </form>
                    }
                />
            </PageContent>
        </>
    )
}

export default TambahAnggota
